import React, { useState } from "react"
import PropTypes from "prop-types"

import Backdrop from "../Backdrop"
import Icon from "@material-ui/core/Icon"
import Button from "@material-ui/core/Button"
import { DeleteForeverOutlined } from "@material-ui/icons"
import estilos from "./estilos.module.css"

const ConfirmModal = (props) => {
	const [isOpen, setIsOpen] = useState(true)
	const { action, okButtonClick, history } = props

	let modalContent
	switch (action) {
		case "delete":
			modalContent = (
				<div>
					<Icon aria-label="deletar">
						<DeleteForeverOutlined fontSize="large" />
					</Icon>
					<h3>Você realmente deseja deletar este registro?</h3>
				</div>
			)
			break
		default:
			break
	}

	const okButtonClickHandler = () => {
		setIsOpen(false)
		okButtonClick()
	}

	const cancelButtonClickHandler = () => history.goBack()

	return (
		<div
			style={
				!isOpen
					? {
							display: "none",
					  }
					: {}
			}
			className={estilos.Container}
		>
			<Backdrop show={isOpen} clicked={cancelButtonClickHandler} zIndex={"4"} />
			<div
				className={estilos.Modal}
				style={{
					transform: isOpen ? "translateY(0)" : "translateY(-100vh)",
					opacity: isOpen ? "1" : "0",
					zIndex: isOpen ? "2000" : "-100",
				}}
			>
				{modalContent}
				<div className={estilos.ModalButtons}>
					<Button
						variant="contained"
						className={estilos.button}
						onClick={cancelButtonClickHandler}
					>
						Cancelar
					</Button>
					<Button
						variant="text"
						color="primary"
						className={estilos.button}
						onClick={okButtonClickHandler}
					>
						Confirmar
					</Button>
				</div>
			</div>
		</div>
	)
}

ConfirmModal.propTypes = {
	action: PropTypes.string,
	okButtonClick: PropTypes.func,
}

export default ConfirmModal
