import React, { useState, useEffect } from "react"

import Modal from "../UI/Modal"
import Tabs from "./Tabs"
import classes from "./estilos.module.css"

const ModalForm = props => {
	const [isOpen, setIsOpen] = useState(true)
	const { camposForm, initialValues, history, loading, location, onFormSubmit, tabLabel, validationSchema, zIndex } = props

	useEffect(() => {
		setIsOpen(location[1] ? true : false)
	}, [location])

	useEffect(() => {
		if (!isOpen) {
			history.goBack()
		}
	}, [isOpen, location, history])

	return (
		<Modal
			className={classes.FormScreen}
			isModalOpen={isOpen}
			setIsModalOpen={setIsOpen}
			zIndex={zIndex}
		>
			<Tabs
				camposForm={isOpen ? camposForm : []}
				setIsModalOpen={setIsOpen}
				initialValues={initialValues}
				isModalOpen={isOpen}
				loading={loading}
				onFormSubmit={onFormSubmit}
				tabLabel={tabLabel}
				validationSchema={validationSchema}
			/>
		</Modal>
	)
}

export default ModalForm
