import React, { useState } from 'react'

import { useDropzone } from 'react-dropzone'
import Button from '@material-ui/core/Button'
import FilesList from '../../components/Dropzone/FileList'
import Modal from '../../components/UI/Modal'
import estilos from './estilos.module.css'

const Dropzone = (props) => {
	const { onChange, maxFiles, height, maxHeight, } = props

	const [filesOk, setFilesOk] = useState([])
	const [isModalOpen, setIsModalOpen] = useState(false)

	const onDrop = files => {
		if (maxFiles) {
			setFilesOk(files.slice(0, maxFiles))
			onChange(files.slice(0, maxFiles))
		} else {
			setFilesOk(files)
			onChange(files)
		}
	}

	let { getRootProps, getInputProps, isDragActive, rejectedFiles } = useDropzone({ onDrop, maxSize: 100000000 })
	const dropzoneClasses = [estilos.Dropzone, isDragActive ? estilos.DropzoneActive : ""]

	if (!rejectedFiles) {
		rejectedFiles = []
	}

	const deleteFile = (i) => {
		const files = filesOk.filter((file, index) => {
			if (index !== i) return file
		})
		setFilesOk(files)
		onChange(files)
		return true
	}

	const files = [
		{ type: "Arquivos rejeitados", files: [...rejectedFiles] },
		{ type: "Arquivos aceitos", files: [...filesOk] },
	]

	return (
		<div className={estilos.ComponentBox}>
			<div
				className={dropzoneClasses.join(" ")}
				{...getRootProps()}
			>
				<input {...getInputProps()} />
				<span className={estilos.Legenda}>Quantidade de arquivos permitidos {maxFiles ? maxFiles : "∞"}</span>
				<br />
				Clique para selecionar arquivos
				<p>ou</p>
				Solte-os aqui

			</div>

			<Button
				className={estilos.DropzoneButton}
				color="primary"
				onClick={() => setIsModalOpen(true)}
				size={"small"}
				variant="contained"
				disabled={filesOk.length > 0 || rejectedFiles.length > 0 ? false : true}
			>
				{maxFiles > 1 ? "Ver arquivos selecionados" : "Ver arquivo selecionado"}
			</Button>

			<Modal
				isModalOpen={isModalOpen}
				height={height}
				margin={"small"}
				maxHeight={maxHeight}
				setIsModalOpen={setIsModalOpen}
			>
				<FilesList
					deleteFile={deleteFile}
					files={files}
				/>

				<div>
					<Button
						className={estilos.CloseButton}
						color="default"
						onClick={() => setIsModalOpen(false)}
						size={"small"}
						variant="contained"
					>
						Fechar lista
				</Button>
				</div>
			</Modal>

		</div>
	)
}

export default Dropzone
