import React from "react"
import PropTypes from "prop-types"

import TextField from "@material-ui/core/TextField"
import Checkbox from "@material-ui/core/Checkbox"
import InputLabel from "@material-ui/core/InputLabel"
import Select from "@material-ui/core/Select"
import Dropzone from "../../Dropzone"
import MenuItem from "@material-ui/core/MenuItem"
import Radio from "@material-ui/core/Radio"
import RadioGroup from "@material-ui/core/RadioGroup"
import FormControlLabel from "@material-ui/core/FormControlLabel"
import FormControl from "@material-ui/core/FormControl"
import estilos from "./estilos.module.css"

const Input = (props) => {
	let inputElement = null

	const inputWidth = (leng) => {
		if (!props.fullWidth) {
			if (leng <= 2) return estilos.Input2
			else if (leng <= 7) return estilos.Input7
			else if (leng <= 10) return estilos.Input10
			else if (leng <= 16) return estilos.Input15
			else if (leng <= 20) return estilos.Input20
			else if (leng <= 30) return estilos.Input30
			else if (leng <= 40) return estilos.Input40
			else if (leng <= 50) return estilos.Input50
			else if (leng <= 60) return estilos.Input60
			else if (leng <= 70) return estilos.Input70
			else if (leng <= 80) return estilos.Input80
		}
	}

	switch (props.type) {
		case "text":
			inputElement = (
				<TextField
					className={[estilos.Input, inputWidth(props.config.max)].join(" ")}
					disabled={props.config.disabled}
					error={props.error}
					fullWidth={props.fullWidth}
					helperText={props.helperText}
					label={props.label}
					margin="dense"
					inputProps={{
						maxlength: props.config.max,
					}}
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					type="text"
					value={props.value}
					variant={!props.variant ? "standard" : props.variant}
				/>
			)
			break

		case "password":
			inputElement = (
				<TextField
					className={[estilos.Input, inputWidth(props.config.max)].join(" ")}
					disabled={props.config.disabled}
					error={props.error}
					fullWidth={props.fullWidth}
					helperText={props.helperText}
					inputProps={{
						maxlength: props.config.max,
					}}
					label={props.label}
					margin="dense"
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					type="password"
					value={props.value}
					variant={!props.variant ? "standard" : props.variant}
				/>
			)
			break

		case "number":
			inputElement = (
				<TextField
					className={[estilos.Input, inputWidth(props.config.max)].join(" ")}
					disabled={props.config.disabled}
					error={props.error}
					fullWidth={props.fullWidth}
					helperText={props.helperText}
					inputProps={{
						min: props.config.min || 0,
					}}
					label={props.label}
					margin="dense"
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					type="number"
					value={props.value}
					variant={!props.variant ? "standard" : props.variant}
				/>
			)
			break

		case "hidden":
			inputElement = (
				<input
					disabled={true}
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					type="hidden"
					value={props.value}
				/>
			)
			break

		case "email":
			inputElement = (
				<TextField
					className={[estilos.Input, inputWidth(props.config.max)].join(" ")}
					disabled={props.config.disabled}
					error={props.error}
					fullWidth={props.fullWidth}
					helperText={props.helperText}
					inputProps={{
						maxlength: props.config.max,
					}}
					label={props.label}
					margin="dense"
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					type="email"
					value={props.value}
					variant={!props.variant ? "standard" : props.variant}
				/>
			)
			break

		case "date":
			inputElement = (
				<TextField
					className={estilos.Date}
					disabled={props.config.disabled}
					error={props.error}
					helperText={props.helperText}
					label={props.label}
					name={props.name}
					onChange={(event) => props.onChange(event)}
					type="date"
					value={props.value}
					InputLabelProps={{
						shrink: true,
					}}
				/>
			)
			break

		case "radio":
			inputElement = (
				<FormControl component="fieldset" className={estilos.Fieldset}>
					<RadioGroup
						name={props.name}
						aria-label={props.label}
						value={String(props.value)}
						className={estilos.RadioGroup}
						onBlur={props.blur}>
						<span className={estilos.RadioLabel}>{props.label}</span>
						<div className={estilos.DirectionRow}>
							{props.options.map((op) => (
								<FormControlLabel
									control={<Radio color={"primary"} />}
									disabled={props.config.disabled}
									key={op.label}
									label={op.label}
									onClick={() => props.onChange(op.value)}
									value={op.value}
								/>
							))}
						</div>
					</RadioGroup>
				</FormControl>
			)
			break

		case "checkbox":
			inputElement = (
				<FormControlLabel
					control={
						<Checkbox
							{...props}
							checked={!!props.value}
							key={props.name}
							onChange={() => props.onChange(!props.value)}
							color="primary"
							inputProps={{
								"aria-label": "Checkbox: " + props.label,
							}}
						/>
					}
					label={props.label}
				/>
			)
			break

		case "textarea":
			inputElement = (
				<TextField
					className={estilos.TextArea}
					disabled={props.config.disabled}
					error={props.error}
					fullWidth={false}
					label={props.label}
					helperText={props.helperText}
					margin="dense"
					multiline
					name={props.name}
					onChange={(event) => props.onChange(event.target.value)}
					rows={props.config.rows && props.config.rows}
					rowsMax="5"
					value={props.value.split("/n").join("\n")}
					variant="outlined"
				/>
			)
			break

		case "select":
			inputElement = (
				<div className={estilos.SelectWithLabel}>
					<InputLabel htmlFor={props.label} className={estilos.SelectLabel}>
						{props.label}
					</InputLabel>
					<Select
						value={props.value}
						className={estilos.Select}
						onChange={(event) => props.onChange(event.target.value)}
						disabled={props.config.disabled}
						inputProps={{
							name: props.name,
							id: props.name,
						}}>
						{props.options.map((op) => (
							<MenuItem key={op.id} value={op.id}>
								{op.label}
							</MenuItem>
						))}
					</Select>
				</div>
			)
			break

		case "autocomplete":
			inputElement = (
				<div className={estilos.SelectWithLabel}>
					<InputLabel htmlFor={props.label} className={estilos.SelectLabel}>
						{props.label}
					</InputLabel>
					<Select
						value={props.value}
						className={estilos.Select}
						onChange={(event) => props.onChange(event.target.value)}
						disabled={props.config.disabled}
						inputProps={{
							name: props.name,
							id: props.name,
						}}>
						{props.options.map((op) => (
							<MenuItem key={op.id} value={op.id}>
								{op.label}
							</MenuItem>
						))}
					</Select>
				</div>
			)
			break

		case "file":
			inputElement = <Dropzone {...props} />
			break

		default:
			inputElement = <div>Missing input type</div>
	}

	return <React.Fragment>{inputElement}</React.Fragment>
}

const areEqual = (props, next) =>
	props.name !== next.name && props.value !== next.value

Input.propTypes = {
	type: PropTypes.string.isRequired,
	helperText: PropTypes.string,
	fullWidth: PropTypes.bool,
	blur: PropTypes.func,
	error: PropTypes.bool,
	onChange: PropTypes.func,
	config: PropTypes.object,
	options: PropTypes.array,
	label: PropTypes.string,
	variant: PropTypes.string,
	name: PropTypes.string.isRequired,
	value: PropTypes.any,
}

export default React.memo(Input, areEqual)
