import React from "react"

import estilos from "./estilos.module.css"

const FormComponents = (props) => {
	let component = null

	const { type, label } = props

	switch (type) {
		case "label":
			component = <h4 className={estilos.ModalLabel}>{label}</h4>
			break

		default:
			component = <div>Missing component type</div>
	}

	return <React.Fragment>{component}</React.Fragment>
}

const areEqual = (props, next) => props.options !== next.options

export default React.memo(FormComponents, areEqual)
