import React from "react"

import Icon from "@material-ui/core/Icon"
import DescriptionIcon from "@material-ui/icons/Description"
import estilos from "./estilos.module.css"

const FileList = (props) => {
	let hasFiles = false

	const { files, deleteFile } = props

	const filesList = files.map((fileType, index) => (
		<div className={estilos.ContainerList} key={index}>
			{fileType.files.length > 0 ? <h3>{fileType.type}</h3> : null}
			{fileType.type === "Arquivos rejeitados" ? (
				<h5>Tamanho máximo 100 MB ou 100000000 KB</h5>
			) : null}

			<div className={estilos.DropzoneFilesList}>
				{fileType.files.map((file, index) => {
					hasFiles = true
					return (
						<div className={estilos.DropzoneListItem} key={index}>
							<Icon
								color={
									fileType.type === "Arquivos rejeitados"
										? "disabled"
										: "primary"
								}
								fontSize={"inherit"}
							>
								<DescriptionIcon />
							</Icon>

							<div className={estilos.DropzoneItemInfo}>
								<div className={estilos.DropzoneItemName}>{file.name}</div>
								<span className={estilos.DropzoneItemSize}>
									{file.size / 1000} KB &nbsp;
								</span>
								{fileType.type === "Arquivos aceitos" ? (
									<div
										className={estilos.DropzoneItemDelete}
										onClick={() => deleteFile(index)}
									>
										Excluir
									</div>
								) : null}
							</div>
						</div>
					)
				})}
			</div>
		</div>
	))

	return (
		<div className={estilos.Container}>
			{hasFiles ? filesList : <h3>Nenhum arquivo foi selecionado</h3>}
		</div>
	)
}

export default FileList
