import React, { useEffect } from "react"
import { Form, Field, FormSpy } from "react-final-form"
import { OnChange } from "react-final-form-listeners"

import { makeStyles, useTheme } from "@material-ui/core/styles"
import AppBar from "@material-ui/core/AppBar"
import Tabs from "@material-ui/core/Tabs"
import Tab from "@material-ui/core/Tab"
import Typography from "@material-ui/core/Typography"
import Grid from "@material-ui/core/Grid"
import Button from "@material-ui/core/Button"
import CircularProgress from "@material-ui/core/CircularProgress"
import Input from "../../UI/Input"
import Component from "../../UI/FormComponents"
import SwipeableViews from "react-swipeable-views"
import estilos from "./estilos.module.css"

function TabPanel(props) {
	const { children, value, index, ...other } = props

	return (
		<Typography
			aria-labelledby={`scrollable-auto-tab-${index}`}
			className={estilos.Container}
			component="div"
			id={`scrollable-auto-tabpanel-${index}`}
			hidden={value !== index}
			role="tabpanel"
			{...other}>
			{children}
		</Typography>
	)
}

function a11yProps(index, label) {
	return {
		id: `full-width-tab-${label}-${index}`,
		"aria-controls": `scrollable-auto-tabpanel-${index}`,
	}
}

const useStyles = makeStyles((theme) => ({
	root: {
		backgroundColor: theme.palette.background.paper,
		width: "100%",
		overscrollBehavior: "none",
	},
}))

const EnhancedTabs = (props) => {
	const classes = useStyles()
	const theme = useTheme()
	const [value, setValue] = React.useState(0)

	const {
		isModalOpen,
		onFormSubmit,
		tabLabel,
		initialValues,
		validationSchema,
		camposForm,
		setIsModalOpen,
		loading,
	} = props

	const handleChangeTab = (event, newValue) => setValue(newValue)

	useEffect(() => {
		setValue(0)
	}, [isModalOpen])

	const handleChangeIndex = (index) => {
		setValue(index)
	}

	const tabsButton = tabLabel.map((tab, index) => (
		<Tab label={tab.label} key={index} {...a11yProps(index, tab.label)} />
	))

	return (
		<div className={classes.root}>
			<AppBar
				color="default"
				key={`tabs-${(Math.random() * 1000000).toFixed()}`}
				position="static">
				<Tabs
					indicatorColor="primary"
					key={`tabs-${(Math.random() * 1000000).toFixed()}`}
					onChange={handleChangeTab}
					scrollButtons={"on"}
					textColor="primary"
					value={value}
					variant={tabLabel.length > 2 ? "scrollable" : "fullWidth"}>
					{tabsButton}
				</Tabs>
			</AppBar>

			<Form
				initialValues={initialValues}
				onSubmit={onFormSubmit}
				subscription={{ submitting: true }}
				validate={async (values) => {
					const validator = validationSchema(values)
					try {
						await validator.validate(values, { abortEarly: false })
					} catch (err) {
						const errors = err.inner.reduce(
							(formError, innerError) => ({
								...formError,
								[innerError.path]: innerError.message,
							}),
							{}
						)

						return errors
					}
				}}>
				{({ handleSubmit, values }) => (
					<form onSubmit={handleSubmit} autocomplete="off">
						<SwipeableViews
							axis={"x"}
							disabled={true}
							index={value}
							key={`swipeable-views-${(Math.random() * 1000000).toFixed()}`}
							onChangeIndex={handleChangeIndex}>
							{camposForm.map((tab, index) => (
								<TabPanel
									value={value}
									key={index}
									index={index}
									dir={theme.direction}>
									<Grid
										alignItems="flex-start"
										className={estilos.Grid}
										container
										direction="row"
										justify="flex-start"
										key={`grid-${index}-${(
											Math.random() * 10000000
										).toFixed()}`}>
										{tab.map((grid, index) => (
											<Grid
												alignItems="flex-start"
												container
												item
												key={`subgrid-${index}-${(
													Math.random() * 10000000
												).toFixed()}`}
												direction={grid.type}
												justify="flex-start"
												xs={grid.config ? grid.config.xs : 12}
												sm={grid.config ? grid.config.sm : 4}
												xl={grid.config ? grid.config.xl : 4}>
												{grid.fields.map((inp) => {
													if (inp.family === "component") {
														return (
															<Component
																{...inp}
																key={`component-${inp.type}-${(
																	Math.random() * 10000000
																).toFixed()}`}
																index={index}
															/>
														)
													} else if (inp.type === "rule") {
														return (
															<Field
																key={`${inp.set}-${inp.field}-${inp.to}-${inp.becomes}-rule-${index}`}
																name={inp.set}
																subscription={{}}>
																{({ input: { onChange } }) => (
																	<FormSpy subscription={{}}>
																		{({ form }) => (
																			<OnChange name={inp.field}>
																				{(value) => {
																					if (value === inp.becomes) {
																						onChange(inp.to)
																					}
																				}}
																			</OnChange>
																		)}
																	</FormSpy>
																)}
															</Field>
														)
													} else if (inp.family === "input") {
														return (
															<Field
																key={inp.name}
																name={inp.name}
																parse={(val) => {
																	if (!val && typeof val !== "boolean") {
																		return ""
																	}
																	if (!inp.config.format) {
																		return val
																	}
																	return inp.config.format(val)
																}}>
																{({ input, meta }) => (
																	<Input
																		{...inp}
																		{...input}
																		disabled={props.loading}
																		error={
																			meta.touched && meta.error ? true : false
																		}
																		helperText={meta.error}
																	/>
																)}
															</Field>
														)
													}
												})}
											</Grid>
										))}
									</Grid>
								</TabPanel>
							))}
						</SwipeableViews>
						<div className={estilos.Footer}>
							<Button
								variant="contained"
								className={estilos.Button}
								disabled={loading}
								onClick={() => {
									setIsModalOpen(false)
								}}>
								Cancelar
							</Button>
							<Button
								type="submit"
								variant="contained"
								color="primary"
								className={estilos.Button}
								disabled={loading}>
								Salvar
								{loading && (
									<CircularProgress
										size={24}
										className={estilos.ButtonProgress}
									/>
								)}
							</Button>
						</div>
					</form>
				)}
			</Form>
		</div>
	)
}

export default EnhancedTabs
