export const booleanToNumber = async (values) => {
	let newValues = values

	for (const key in values) {
		if (typeof values[key] === "boolean") {
			newValues = {
				...newValues,
				[key]: values[key] ? 1 : 0,
			}
		}
	}

	return newValues
}

export const enterToCode = async (values) => {
	let newValues = values

	for (const key in values) {
		if (typeof values[key] === "string") {
			let a = values[key].split("\n").join("/n")

			newValues = {
				...newValues,
				[key]: a,
			}
		}
	}

	return newValues
}

export const formatFormData = async (values) => {
	return booleanToNumber(values)
		.then(enterToCode)
		.then((newValues) => newValues)
		.catch((err) => console.log(err))
}
