import React, { useEffect } from 'react'

import estilos from './estilos.module.css'
import Backdrop from '../Backdrop/'

const Modal = props => {
	const { children, isModalOpen, margin, setIsModalOpen, zIndex } = props

	useEffect(() => {
		if (isModalOpen) {
			document.body.style.overflow = "hidden";
		} else {
			document.body.style.overflow = "auto";
		}

	}, [isModalOpen])

	let z = "1130"
	if (zIndex === "2") {
		z = "1131"
	} else {
		if (zIndex === "3") {
			z = "1132"
		}
	}

	return (
		<div style={!isModalOpen ? {
			display: 'none',
		} : {}}
			className={estilos.Container}
		>
			<Backdrop show={isModalOpen} clicked={() => setIsModalOpen(false)} zIndex={zIndex} />
			<div
				className={[estilos.Modal, !!margin ? estilos.ModalMarginSmall : estilos.ModalMarginCenter].join(" ")}
				style={{
					opacity: isModalOpen ? '1' : '0',
					transform: isModalOpen ? 'translateY(0)' : 'translateY(-100vh)',
					zIndex: isModalOpen ? z : '-100',
				}}>
				{children}
			</div>
		</div >
	)
}

export default Modal
